import React, { useState } from 'react'
import './app.css'
import 'bootstrap/dist/css/bootstrap.min.css';
import Switch from '@material-ui/core/Switch';

 const App = () => {
  const [state,setstate]=useState(false);
const handleChange=()=>{
  setstate(!state);
}

  const sound=(item)=>{
    if(state===true){
    if(item=='a'){
    const audio=document.getElementById('aud1');
    audio.currentTime=0;
    audio.play();
    }
    if(item=='b'){
      const audio=document.getElementById('aud2');
      audio.currentTime=0;
      audio.play();
      }
      if(item=='c'){
        const audio=document.getElementById('aud3');
        audio.currentTime=0;
        audio.play();
        }
        if(item=='d'){
          const audio=document.getElementById('aud4');
          audio.currentTime=0;
          audio.play();
          }
          if(item=='e'){
            const audio=document.getElementById('aud5');
            audio.currentTime=0;
            audio.play();
            }
            if(item=='f'){
              const audio=document.getElementById('aud6');
              audio.currentTime=0;
              audio.play();
              }

              if(item=='g'){
                const audio=document.getElementById('aud7');
                audio.currentTime=0;
                audio.play();
                }
                if(item=='h'){
                  const audio=document.getElementById('aud8');
                  audio.currentTime=0;
                  audio.play();
                  }
                  if(item=='i'){
                    const audio=document.getElementById('aud9');
                    audio.currentTime=0;
                    audio.play();
                    }
                  }
                  
  }
  return (
    <div className="size">
<h1>DRUM MACHINE</h1>
<br></br>
<br></br>
POWER
<Switch
        checked={state}
        onChange={handleChange}
        name="checkedA"
        size="x-large"
        color="primary"
        inputProps={{ 'aria-label': 'secondary checkbox' }}
      ></Switch>

<br></br>
<br></br>

    <div className="layout">
    <div className="box" onClick={()=>{sound('a')}}>
            <audio src='https://s3.amazonaws.com/freecodecamp/drums/Heater-1.mp3'id="aud1" ></audio>
            <p>A</p>
              </div>

              <div className="box" onClick={()=>{sound('b')}}>
            <audio src='https://s3.amazonaws.com/freecodecamp/drums/Dsc_Oh.mp3' id="aud2" ></audio>
            <p>B</p>
              </div>

              <div className="box" onClick={()=>{sound('c')}}>
            <audio src='https://s3.amazonaws.com/freecodecamp/drums/Kick_n_Hat.mp3' id="aud3" ></audio>
            <p>C</p>
              </div>
    </div>
              <div className="layout">
            

              <div className="box" onClick={()=>{sound('d')}}>
            <audio src='https://s3.amazonaws.com/freecodecamp/drums/RP4_KICK_1.mp3' id="aud4" ></audio>
            <p>D</p>
              </div>
              <div className="box" onClick={()=>{sound('e')}}>
            <audio src='https://s3.amazonaws.com/freecodecamp/drums/Chord_2.mp3'id="aud5" ></audio>
            <p>E</p>
              </div>
              <div className="box" onClick={()=>{sound('f')}}>
            <audio src='https://s3.amazonaws.com/freecodecamp/drums/Give_us_a_light.mp3' id="aud6" ></audio>
            <p>F</p>
              </div>
              </div>

              <div className="layout">
    
              <div className="box" onClick={()=>{sound('g')}}>
            <audio src='https://s3.amazonaws.com/freecodecamp/drums/Dry_Ohh.mp3' id="aud7" ></audio>
            <p>G</p>
              </div>
              <div className="box" onClick={()=>{sound('h')}}>
            <audio src='https://s3.amazonaws.com/freecodecamp/drums/Bld_H1.mp3'id="aud8" ></audio>
            <p>H</p>
              </div>
              <div className="box" onClick={()=>{sound('i')}}>
            <audio src='https://s3.amazonaws.com/freecodecamp/drums/Heater-6.mp3' id="aud9" ></audio>
            <p>I</p>
              </div>
    </div>
   
      </div>
  )
}
export default App;
